import Common from '../../../../../assets/js/common.js';
export default {
  props: {
    leftOptions: {
      type: Object,
      default () {
        return {
          showBack: true,
          backText: 'Back',
          preventGoBack: false
        }
      }
    },
    leftType:{
       type:String,
       default:'1'
    },
    toLink:{
      type:String,
      default:''
    },
    title: String,
    transition: String,
    rightOptions: {
      type: Object,
      default () {
        return {
          showMore: false
        }
      }
    }
  },
  data(){
     return{
        ua:Common.ua
     }
  },
  mounted(){

  },
  filters:{
     cityName(val){
        return val!=''&&val!=null&&val!=undefined ? val.replace(/(市|市市辖区)$/g,''):''
     }
  },
  methods: {
    onClickBack () {
      if (this.leftOptions.preventGoBack && this.toLink) {
        window.location.href=this.toLink;
        //this.$emit('on-click-back')
      } else {
        this.$router ? this.$router.back() : window.history.back()
      }
    }
  }
}



