export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    text: {
      type:String,
      default:''
    },
    transparent:{
      type:Boolean,
      default:true
    },
    position: String
  },
  created () {

    this.show = this.value
  },
  data () {
    return {
      show: false
    }
  },
  methods:{

  },
  watch: {
    value (val) {
      this.show = val;

    },
    show (val) {
      this.$emit('input', val)
    }
  }
}



